import { GAME_STATE, STONE } from "./global";

export class GomokuNarabe {
    #field: number[][];
    #turn: number;
    #size: number;

    constructor(n: number) {
        this.#size = n;
        this.#turn = STONE.FIRST;
        this.#field = new Array(n + 2)
            .fill(null)
            .map((_) => new Array(n + 2).fill(STONE.NONE));
        for (let i = 0; i < n + 2; i++) {
            this.#field[i][0] = STONE.WALL;
            this.#field[i][n + 1] = STONE.WALL;
            this.#field[0][i] = STONE.WALL;
            this.#field[n + 1][i] = STONE.WALL;
        }
    }

    get field() {
        return this.#field.slice(1, -1).map((arr) => arr.slice(1, -1));
    }
    get turn() {
        return this.#turn;
    }
    get size() {
        return this.#size;
    }

    getOpponentTurn() {
        return this.turn === STONE.FIRST ? STONE.SECOND : STONE.FIRST;
    }

    putStone(row: number, col: number) {
        if (this.#field[row][col] !== STONE.NONE) {
            return null;
        } else {
            this.#field[row][col] = this.#turn;
            const state = this.checkGameState(
                row,
                col,
                this.#field,
                this.#turn
            );
            this.#turn = this.getOpponentTurn();
            return state;
        }
    }

    checkGameState(r: number, c: number, field: number[][], turn: number) {
        if (countStone([0, 1]) + countStone([0, -1]) >= 4) {
            return GAME_STATE.WIN;
        } else if (countStone([1, 0]) + countStone([-1, 0]) >= 4) {
            return GAME_STATE.WIN;
        } else if (countStone([1, 1]) + countStone([-1, -1]) >= 4) {
            return GAME_STATE.WIN;
        } else if (countStone([1, -1]) + countStone([-1, 1]) >= 4) {
            return GAME_STATE.WIN;
        } else if (
            field.every((arr) => {
                return arr.every((e) => e !== STONE.NONE);
            })
        ) {
            return GAME_STATE.DRAW;
        } else {
            return GAME_STATE.PENDING;
        }

        //diræ–¹å‘ã«åŒç¨®ã®çŸ³ãŒã„ãã¤é€£ç¶šã—ã¦ã‚ã‚‹ã‹æ•°ãˆã‚‹
        function countStone(dir: [number, number]) {
            let row = r;
            let col = c;
            let count = 0;
            rec();
            function rec() {
                row += dir[0];
                col += dir[1];
                if (field[row][col] !== turn) {
                    return;
                }
                count++;
                rec();
            }
            return count;
        }
    }

    //COMã®æ¬¡ã®ä¸€æ‰‹
    comNext() {
        //boxã«ã¯ç©ºã®ãƒã‚¹ã®ãƒªã‚¹ãƒˆã‚’å…¥ã‚Œã‚‹
        let box: number[] | [] = [];
        for (let i = 1; i <= this.#size; i++) {
            for (let j = 1; j <= this.#size; j++) {
                if (this.#field[i][j] == STONE.NONE) {
                    box.push([i, j] as never);
                }
            }
        }

        for (let elm of box) {
            let [i, j] = [elm[0], elm[1]];
            let field = structuredClone(this.#field);
            field[i][j] = this.turn;
            if (
                this.checkGameState(i, j, field, this.turn) === GAME_STATE.WIN
            ) {
                return [i - 1, j - 1];
            }
        }

        for (let elm of box) {
            let [i, j] = [elm[0], elm[1]];
            let field = structuredClone(this.#field);
            field[i][j] = this.getOpponentTurn();
            if (
                this.checkGameState(i, j, field, this.getOpponentTurn()) ===
                GAME_STATE.WIN
            ) {
                return [i - 1, j - 1];
            }
        }

        for (let elm of box) {
            let [i, j] = [elm[0], elm[1]];
            let field = structuredClone(this.#field);
            field[i][j] = this.turn;
            const gsa = this.getStoneArray(i, j, field, this.turn);
            let idx = gsa.counts.findIndex((e) => e == 3);
            if (idx >= 0 && gsa.noneEnds[idx] == 2) {
                return [i - 1, j - 1];
            }
        }

        for (let elm of box) {
            let [i, j] = [elm[0], elm[1]];
            let field = structuredClone(this.#field);
            field[i][j] = this.turn;
            const gsa = this.getStoneArray(i, j, field, this.turn);
            let idx = gsa.counts.findIndex((e) => e == 3);
            let idx2 = gsa.counts.findIndex((e) => e == 2);
            if (
                idx >= 0 &&
                gsa.noneEnds[idx] == 1 &&
                idx2 >= 0 &&
                gsa.noneEnds[idx2] == 2
            ) {
                return [i - 1, j - 1];
            }
        }

        for (let elm of box) {
            let [i, j] = [elm[0], elm[1]];
            let field = structuredClone(this.#field);
            field[i][j] = this.turn;
            const gsa = this.getStoneArray(i, j, field, this.turn);
            let idx = gsa.counts.findIndex((e) => e == 3);
            if (idx >= 0 && gsa.noneEnds[idx] == 1) {
                if (Math.random() < 0.5) {
                    continue;
                }
                return [i - 1, j - 1];
            }
        }

        //ç›¸æ‰‹ã®4é€£ã‚’å°ã˜ã‚‹
        for (let elm of box) {
            let [i, j] = [elm[0], elm[1]];
            let field = structuredClone(this.#field);
            field[i][j] = this.getOpponentTurn();
            const gsa = this.getStoneArray(i, j, field, this.getOpponentTurn());
            let idx = gsa.counts.findIndex((e) => e == 3);
            if (idx >= 0 && gsa.noneEnds[idx] == 2) {
                return [i - 1, j - 1];
            }
        }
        //ç›¸æ‰‹ã®4-3ã‚’å°ã˜ã‚‹
        for (let elm of box) {
            let [i, j] = [elm[0], elm[1]];
            let field = structuredClone(this.#field);
            field[i][j] = this.getOpponentTurn();
            const gsa = this.getStoneArray(i, j, field, this.getOpponentTurn());
            let idx = gsa.counts.findIndex((e) => e == 3);
            let idx2 = gsa.counts.findIndex((e) => e == 2);
            if (
                idx >= 0 &&
                gsa.noneEnds[idx] == 1 &&
                idx2 >= 0 &&
                gsa.noneEnds[idx2] == 2
            ) {
                return [i - 1, j - 1];
            }
        }

        for (let elm of box) {
            let [i, j] = [elm[0], elm[1]];
            let field = structuredClone(this.#field);
            field[i][j] = this.turn;
            const gsa = this.getStoneArray(i, j, field, this.turn);

            let idx = gsa.counts.findIndex((e) => e == 2);
            if (idx >= 0 && gsa.noneEnds[idx] == 2) {
                return [i - 1, j - 1];
            }
        }

        for (let elm of box) {
            let [i, j] = [elm[0], elm[1]];
            let field = structuredClone(this.#field);
            field[i][j] = this.getOpponentTurn();
            const gsa = this.getStoneArray(i, j, field, this.getOpponentTurn());

            let idx = gsa.counts.findIndex((e) => e == 2);
            if (idx >= 0 && gsa.noneEnds[idx] == 2) {
                return [i - 1, j - 1];
            }
        }

        for (let elm of box) {
            let [i, j] = [elm[0], elm[1]];
            let field = structuredClone(this.#field);
            field[i][j] = this.turn;
            const gsa = this.getStoneArray(i, j, field, this.turn);
            let idx2 = gsa.counts.findIndex((e) => e == 1);
            let idx3 = gsa.counts.findIndex((e, i) => e == 1 && i > idx2);
            if (
                idx2 >= 0 &&
                gsa.noneEnds[idx2] == 2 &&
                idx3 >= 0 &&
                gsa.noneEnds[idx3] == 2
            ) {
                return [i - 1, j - 1];
            }
        }

        let midIndex = Math.floor(box.length / 2);
        let [i, j] = [box[midIndex][0], box[midIndex][1]];
        return [i - 1, j - 1];
    }

    //æ¨ªæ–¹å‘ã€ç¸¦æ–¹å‘ã€æ–œã‚æ–¹å‘Ã—2ã€€ã®ï¼”æ–¹å‘ã«ã¤ã„ã¦åŒç¨®ã®çŸ³ãŒé€£ç¶šã—ã¦ã„ã‚‹æ•°ãŠã‚ˆã³ãã®ä¸¡ç«¯ãŒç©ºãƒã‚¹ã§ã‚ã‚‹æ•°ã‚’è¨ˆç®—ã—ã¦ãƒªã‚¹ãƒˆã«ã—ã¦è¿”ã™ã€‚
    getStoneArray(r: number, c: number, field: number[][], turn: number) {
        let countBox = [];
        let noneEndBox = [];
        countBox.push(
            (countStone([0, 1]).count + countStone([0, -1]).count) as never
        );
        noneEndBox.push(
            (countStone([0, 1]).noneEnd + countStone([0, -1]).noneEnd) as never
        );
        countBox.push(
            (countStone([1, 0]).count + countStone([-1, 0]).count) as never
        );
        noneEndBox.push(
            (countStone([1, 0]).noneEnd + countStone([-1, 0]).noneEnd) as never
        );
        countBox.push(
            (countStone([1, 1]).count + countStone([-1, -1]).count) as never
        );
        noneEndBox.push(
            (countStone([1, 1]).noneEnd + countStone([-1, -1]).noneEnd) as never
        );
        countBox.push(
            (countStone([1, -1]).count + countStone([-1, 1]).count) as never
        );
        noneEndBox.push(
            (countStone([1, -1]).noneEnd + countStone([-1, 1]).noneEnd) as never
        );

        return { counts: countBox, noneEnds: noneEndBox };

        //diræ–¹å‘ã«åŒç¨®ã®çŸ³ãŒé€£ç¶šã—ã¦ä½•å€‹ã‚ã‚‹ã‹(count)ã€ã•ã‚‰ã«ãã®ç›´å¾ŒãŒç©ºãƒã‚¹ã‹ã©ã†ã‹(ç©ºãƒã‚¹ã®ã¨ãnoneEndã¯1)ã‚’è¿”ã™ã€‚
        function countStone(dir: [number, number]) {
            let row = r;
            let col = c;
            let count = 0;
            let noneEnd = 0;
            rec();
            function rec() {
                row += dir[0];
                col += dir[1];
                if (field[row][col] !== turn) {
                    noneEnd = field[row][col] == STONE.NONE ? 1 : 0;
                    return;
                }
                count++;
                rec();
            }
            return { count: count, noneEnd: noneEnd };
        }
    }
}
